.messageWrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  border-radius: 10px;
  max-width: 44%;
  margin: 0.6em 3em;
  color: rgb(255, 255, 255);
  padding: 0;
  box-shadow: 0 0 6px 0 rgb(65, 64, 64);
}

.user {
  font-size: 0.9em;
  margin-bottom: 0.1em;
  padding: 0.2em 2em;
  text-align: center;
  border-color: black;
  margin-top: 0;
  font-weight: 400;
  border-top-left-radius: 9px;
  border-top-right-radius: 9px;
}

.text {
  padding: 0 1em;
  color: rgb(0, 0, 0);
}

.container {
  display: flex;
}

.time {
  font-size: 0.7em;
  letter-spacing: 2px;
  padding: 0 1em;
  text-align: right;
  color: rgb(104, 101, 101);
  margin-top: 0;
}

.self {
  background-color: #b9ebff;
  align-self: flex-end;
  .user {
    background-color: #1d4461;
  }
}

.anotherUser {
  background-color: rgb(224, 255, 189);
  align-self: flex-start;
  .user {
    background-color: rgb(0, 85, 74);
  }
}

.isViewed {
  position: absolute;
  width: 1.4em;
  top: 50%;
  left: -20%;
  transform: translate(-50%, -50%);
}

.contextMenu {
  position: absolute;
  color: black;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(236, 236, 236);
  border-radius: 10px;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  text-align: center;
  p {
    font-size: 0.9em;
    margin: 0;
    padding: 0.5em 0;
    cursor: pointer;
    transition: all 0.2s;
    &:hover {
      background-color: rgb(126, 126, 131);
      color: white;
      font-weight: 800;
      border-top: 1px solid rgb(39, 39, 39);
      border-bottom: 1px solid rgb(39, 39, 39);
    }
  }
}
